@use '../abstracts/typography' as tp;
@use '../abstracts/colors' as clr;

.field-box {
  display: grid;
  row-gap: 0.5rem;

  &__field {
    display: grid;
    grid-template-columns: 1fr min-content;
    align-items: center;
    column-gap: 1rem;

    border-radius: 5px;
    border: 1px solid clr.$neutral-gray-blue;
    padding: 1rem;
  }

  &__field-input {
    all: unset;
    font-weight: tp.$fw-semi-bold;
    font-size: tp.$fz-text-mobile;
  }

  &__error-icon {
    display: none;
    place-items: center;
  }

  &__error-text {
    font-size: tp.$fz-error-text;
    font-weight: tp.$fw-medium;
    color: clr.$primary-red;

    justify-self: end;
  }
}
